import permision from '@/common/permission.js';
import config from '@/config/config';
var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
export default {
	data() {
		return {
			order_id: 0,
			cardUrl: '', //证件前
			isSubmit: false,
			payTypeIndex: 0, //支付方式
			isPerfect: '' //是否完善信息
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		if (option.orderId != '') this.order_id = option.orderId;
	},
	methods: {
		chooseImage: async function(type) {







			uni.chooseImage({
				sourceType: ['album', 'camera'],
				sizeType: ['compressed'],
				count: 1,
				success: res => {
					this.cardUrl = res.tempFilePaths[0];
				},
				fail: err => {





				}
			});
		},
		previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: [e]
			});
		},
		async checkPermission(code) {
			let type = code ? code - 1 : this.sourceTypeIndex;
			let status = permision.isIOS ?
				await permision.requestIOS('camera') :
				await permision.requestAndroid(type === 0 ? 'android.permission.CAMERA' :
					'android.permission.READ_EXTERNAL_STORAGE');

			if (status === null || status === 1) {
				status = 1;
			} else {
				uni.showModal({
					content: '没有开启权限',
					confirmText: '设置',
					success: function(res) {
						if (res.confirm) {
							permision.gotoAppSetting();
						}
					}
				});
			}

			return status;
		},
		upload() {
			//上传
			var _this = this;
			if (!_this.cardUrl) {
				//消息异常
				uni.showToast({
					icon: 'none',
					title: '请选择收款凭证图片'
				});
				return false;
			}
			_this.isSubmit = true;
			uni.showLoading({
				title: '上传中'
			});
			_this.uploadFile('file', _this.cardUrl);
		},
		uploadFile(name, file) {
			var _this = this;
			//文件上传
			var user = uni.getStorageSync('uerInfo');
			const uploadTask = uni.uploadFile({
				url: config.API_SITE + '/gzh/Pay/addYine',
				filePath: file,
				name: name,
				formData: {
					token: user.token
				},
				success: function(msg) {
					uni.hideLoading();
					_this.isSubmit = false;
					var data = JSON.parse(msg.data);
					if (data.status == 1) {
						_this.addPay(data.data);
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				},
				fail: res => {
					_this.isSubmit = false;
					uni.showToast({
						icon: 'none',
						title: '上传失败'
					});
				}
			});
		},
		addPay(url) {
			var _this = this;
			_this.$request.order
				.setPayDocument({
					order_id: _this.order_id,
					order_state: 2,
					zfb_imgs: url
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						uni.showToast({
							icon: 'none',
							title: '确认成功'
						});
						uni.navigateTo({
							url: '/pages/order/info?orderId=' + _this.order_id
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
